import { HttpHeaders } from '@angular/common/http';

export class Utils {
    static buildSPDKHttpOptions = (sessionKey: string) => ({
        headers: new HttpHeaders({
          'Content-Type': 'application/json',
          'SPDKSessionKey': sessionKey
        })
      });

    static buildDefaultHttpOptions = () => ({
        headers: new HttpHeaders({
            'Content-Type': 'application/json'
          })
    })
}